import React, { memo, useMemo } from "react";

import styled from "styled-components";
import mq from "../../components/MediaQuery";

/**   */
import { useSelector, useDispatch } from "react-redux";


/** chart.js  */
import {
  // 
  Chart,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  // 
  BarElement,
} from "chart.js";

import { Bar } from "react-chartjs-2";

// chart.js import Chart  import  
Chart.register(CategoryScale, LinearScale, Title, Tooltip, Legend, BarElement);


const Graph2Container = styled.div`
  width: 50%;
  //  css
  ${mq.maxWidth("md")`
  width: 100%;
  `}

  .container {
    margin: 10px;
    height: 300px;
  }
`;

const Graph2 = memo(() => {
  /**   */
  const { item } = useSelector((state) => state.TitanicSlice);

  /**    */
  //   
  // , , 
  const { keys, survived, dead } = useMemo(() => {
    if (!item) {
      return { keys: [], values: [], dead: [] };
    }

    console.group("Graph2");

    const ageData = item.reduce((acc, cur) => {
      const ageLevel = `${parseInt(cur.age / 10) * 10}`;

      if (acc[ageLevel] == undefined) {
        acc[ageLevel] = { survived: 0, dead: 0 };
      }
      if (cur.survived) {
        acc[ageLevel].survived++;
      } else {
        acc[ageLevel].dead++;
      }
      return acc;
    }, {});

    console.log(ageData);

    const keys = Object.keys(ageData).sort();
    console.log(keys);

    const survived = keys.map((v, i) => ageData[v].survived);
    console.log(survived);

    const dead = keys.map((v, i) => ageData[v].dead);
    console.log(dead);

    const result = { keys, survived, dead };
    console.log(result);

    console.groupEnd();

    return result;
  }, [item]);

  return (
    <Graph2Container>
      <div className="container">
        {keys && survived && dead && (
          <Bar
            data={{
              labels: keys, //x
              datasets: [
                {
                  label: "",
                  data: survived,
                  backgroundColor: "rgba(53, 162, 235, 0.5)",
                  borderColor: "rgba(53,162, 235, 1)",
                  borderWidth: 1,
                },
                {
                  label: "",
                  data: survived,
                  backgroundColor: "rgba(258, 162, 235, 0.5)",
                  borderColor: "rgba(258,162, 235, 1)",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              plugins: {
                //  
                legend: {
                  position: "bottom",
                },
                // 
                title: {
                  display: true,
                  text: "   ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Bar>
        )}
      </div>

      {/* {item && <p>{JSON.stringify(item).substring(0, 50)}</p>} */}
    </Graph2Container>
  );
});

export default Graph2;
