import React, { memo, useMemo } from "react";

import styled from "styled-components";
import mq from "../../components/MediaQuery";

/**   */
import { useSelector, useDispatch } from "react-redux";

/** chart.js  */
import {
  // 
  Chart,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  // 
  BarElement,
} from "chart.js";

import { Bar } from "react-chartjs-2";

// chart.js import Chart  import  
Chart.register(CategoryScale, LinearScale, Title, Tooltip, Legend, BarElement);

const Graph3Container = styled.div`
  width: 50%;
  //  css
  ${mq.maxWidth("md")`
  width: 100%;
  `}

  .container {
    margin: 10px;
    height: 300px;
  }
`;

const Graph3 = memo(() => {
  /**   */
  const { item } = useSelector((state) => state.TitanicSlice);

  //   (, )
  const sex = useMemo(() => {
    if (!item) {
      return [0, 0];
    }
    const maleData = item.reduce(
      (acc, cur) => {
        acc[cur.sex == "male" ? 0 : 1]++;
        return acc;
      },
      [0, 0]
    );

    console.group("Graph3");
    console.log(maleData);
    console.groupEnd();

    return maleData;
  }, [item]);

  return (
    <Graph3Container>
      <div className="container">
        {sex && (
          <Bar
            data={{
              labels: ["male", "female"], //x
              datasets: [
                {
                  label: "",
                  data: sex,
                  backgroundColor: ["#29ab3688", "#e74c3c88"],
                  borderColor: ["#29ab36", "#e74c3c"],
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              indexAxis: "y", //  x  => y  
              plugins: {
                //  
                legend: {
                  position: "bottom", //  
                },
                // 
                title: {
                  display: true,
                  text: "  ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Bar>
        )}
      </div>
    </Graph3Container>
  );
});

export default Graph3;
