import React, { memo, useMemo } from "react";

import styled from "styled-components";
import mq from "../../components/MediaQuery";

/**   */
import { useSelector, useDispatch } from "react-redux";

/** chart.js  */
import {
  // 
  Chart,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  //  (BarElement)
  // 
  ArcElement,
} from "chart.js";

import { Pie } from "react-chartjs-2";

// chart.js import Chart  import  
Chart.register(CategoryScale, LinearScale, Title, Tooltip, Legend, ArcElement);

const Graph5Container = styled.div`
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  .container {
    height: 500px;
    width: 33.3%;

    // ()css
    ${mq.maxWidth('md')`
      width: 100%;
    `}
  }
`;

const Graph5 = memo(() => {
  /**   */
  const { item } = useSelector((state) => state.TitanicSlice);

  /** (1,2,3) , ,  */
  // , ,  
  const { passingers, survived, dead } = useMemo(() => {
    if (!item) {
      return { passingers: [0, 0, 0], survived: [0, 0, 0], dead: [0, 0, 0] };
    }

    const pclassData = item.reduce(
      (acc, cur) => {

        //    
        const pclass = cur.pclass;
        acc.passingers[pclass - 1]++;

        //        
        //   survived +1 ? dead +1
        if (cur.survived) {
          acc.survived[pclass - 1]++;
        } else {
          acc.dead[pclass - 1]++;
        }
        return acc;
      },
      { passingers: [0, 0, 0], survived: [0, 0, 0], dead: [0, 0, 0] }
    );

    console.group("Graph5)");
    console.log(pclassData);
    console.groupEnd();

    return pclassData;
  }, [item]);

  return (
    <Graph5Container>

      {/*    */}
      <div className="container">
        {passingers && (
          <Pie
            data={{
              labels: ["1", "2", "3"], //x
              datasets: [
                {
                  label: "",
                  data: passingers,
                  backgroundColor: [
                    "rgba(255, 99, 132, 0.6)",
                    "rgba(54, 162, 235, 0.6)",
                    "rgba(255, 206, 132, 0.6)",
                  ],
                  borderColor: [
                    "rgba(255, 99, 132, 1)",
                    "rgba(54, 162, 235, 1)",
                    "rgba(255, 206, 132, 1)",
                  ],
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              plugins: {
                //  
                legend: {
                  position: "left", //  
                },
                // 
                title: {
                  display: true,
                  text: "   ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Pie>
        )}
      </div>


      {/*    */}
      <div className="container">
        {survived && (
          <Pie
            data={{
              labels: ["1", "2", "3"], //x
              datasets: [
                {
                  label: "",
                  data: survived,
                  backgroundColor: [
                    "rgba(255, 99, 132, 0.6)",
                    "rgba(54, 99, 132, 0.6)",
                    "rgba(255, 206, 132, 0.6)",
                  ],
                  borderColor: [
                    "rgba(255, 99, 132, 1)",
                    "rgba(54, 99, 132, 1)",
                    "rgba(255, 206, 132, 1)",
                  ],
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              plugins: {
                //  
                legend: {
                  position: "left", //  
                },
                // 
                title: {
                  display: true,
                  text: "   ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Pie>
        )}
      </div>

      {/*    */}
      <div className="container">
        {dead && (
          <Pie
            data={{
              labels: ["1", "2", "3"], //x
              datasets: [
                {
                  label: "",
                  data: dead,
                  backgroundColor: [
                    "rgba(255, 99, 132, 0.6)",
                    "rgba(54, 99, 132, 0.6)",
                    "rgba(255, 206, 132, 0.6)",
                  ],
                  borderColor: [
                    "rgba(255, 99, 132, 1)",
                    "rgba(54, 99, 132, 1)",
                    "rgba(255, 206, 132, 1)",
                  ],
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              plugins: {
                //  
                legend: {
                  position: "left", //  
                },
                // 
                title: {
                  display: true,
                  text: "   ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Pie>
        )}
      </div>
    </Graph5Container>
  );
});

export default Graph5;
