import React, { memo, useMemo } from "react";

import styled from "styled-components";
import mq from "../../components/MediaQuery";

/**   */
import { useSelector, useDispatch } from "react-redux";


/** chart.js  */
import {
  // 
  Chart,
  CategoryScale,
  LinearScale,
  Title,
  Tooltip,
  Legend,
  // 
  BarElement,
} from "chart.js";

import { Bar } from "react-chartjs-2";

// chart.js import Chart  import  
Chart.register(CategoryScale, LinearScale, Title, Tooltip, Legend, BarElement);



const Graph4Container = styled.div`
  width: 50%;
  //  css
  ${mq.maxWidth("md")`
  width: 100%;
  `}

  .container {
    margin: 10px;
    height: 300px;
  }
`;

const Graph4 = memo(() => {
  /**   */
  const { item } = useSelector((state) => state.TitanicSlice);

  //   
  //  0  1
const {survived, dead} = useMemo(() => {
  if (!item) {
    return {survived: [0, 0], dead: [0,0]};
  }

  const maleData = item.reduce((acc, cur) => {
    const idx = cur.sex == 'male' ? 0 : 1;
    const key = cur.survived ? 'survived' : 'dead';
    acc[key][idx]++;
    return acc;
  }, {survived: [0, 0], dead: [0, 0]});

  console.group("Graph4)");
  console.log(maleData);
  console.groupEnd();

  return maleData;
}, [item]);

  return (
    <Graph4Container>
       <div className="container">
        {survived && dead && (
          <Bar
            data={{
              labels: ["male", "female"], //x
              datasets: [
                {
                  label: "",
                  data: survived,
                  backgroundColor: "rgba(53, 162, 235, 0.5)",
                  borderColor: "rgba(53,162, 235, 1)",
                  borderWidth: 1,
                },
                {
                  label: "",
                  data: dead,
                  backgroundColor: "rgba(258, 162, 235, 0.5)",
                  borderColor: "rgba(258,162, 235, 1)",
                  borderWidth: 1,
                },
              ],
            }}
            options={{
              //   
              responsive: true,
              //     (false   )
              maintainAspectRatio: false,
              indexAxis: "y", //  x  => y  
              plugins: {
                //  
                legend: {
                  position: "bottom", //  
                },
                // 
                title: {
                  display: true,
                  text: "   ",
                  font: {
                    size: 18,
                    color: "#000",
                  },
                },
              },
            }}
          ></Bar>
        )}
      </div>
    </Graph4Container>
  );
});

export default Graph4;
